import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import { getAllDogs, getDbDogs, getTemperaments, postNewDog} from "../redux/actions/actions";
export default function CreateDog(){

    let dispatch = useDispatch()

    useEffect(() => {
        dispatch(getTemperaments())
    },[])

    let [input, setInput] = useState({
        name: "",
        min_weight: undefined,
        max_weight: undefined,
        min_height: undefined,
        max_height: undefined,
        tempsId: [],
        min_life: undefined,
        max_life: undefined,
        image: undefined,
    })


    let [error, setError] = useState({})


    let [preview, setPreview] = useState("")


    function validate(input){
        let error ={}

        if (!input.name) {
            error.name = "Introduzca el nombre del perro"
        }

        if (!input.min_weight) {
            error.min_weight = "Introduzca peso minimo"
        }else if (input.min_weight >= input.max_weight) {
            error.min_weight = "El peso minimo debe ser menor al peso maximo"
        }
        if (!input.max_weight) {
            error.max_weight = "Introduzca peso maximo"
        }
        if (!input.min_height) {
            error.min_height = "Introduzca altura minima"
        }else if (input.min_height >= input.max_height) {
            error.min_height = "La altura minima debe ser menor a la altura maxima"
        }
        if (!input.max_height) {
            error.max_height = "Introduzca altura maxima"
        }

        if (input.min_life && !input.max_life) {
            error.max_life = "Si va a introducir una esperanza de vida minima, tambien introduzca una maxima"
        }else if (!input.min_life && input.max_life) {
            error.min_life = "Si va a introducir una esperanza de vida maxima, tambien introduzca una minima"
        }

        if (input.tempsId.length === 0) {
            error.tempsId = "Introduzca al menos un temperamento"
        }
        return error;
    }


    let temperaments = useSelector(state => state.temperaments)
    let [available, setAvailable] = useState(false)

    function handleInput(e){
        let value = e.target.value
        let inputName = e.target.name
        if (inputName !== "name" && inputName !== "image") {
            value = parseInt(value);
        }
        if (inputName === "tempsId") {
            if (!input.tempsId.includes(value)) {
                setInput((prev) => ({...prev, [inputName]:[...input.tempsId, value]}));
                setAvailable(false)
            }else{
                setAvailable(true)
            }
        }else if (inputName === "image") {
            const reader = new FileReader();
            reader.onload = () => {
                    if (reader.readyState === 2) {
                        setPreview(reader.result);
                    }
            }
            reader.readAsDataURL(e.target.files[0])
            let imagefile = document.querySelector('#file');
            setInput((prev) => ({...prev, [inputName]:imagefile.files[0]}));
        }
        else{
            setInput((prev) => ({...prev, [inputName]:value}));
        }
    }

    let [incompleted, setIncompleted] = useState(null)

    function validateErrors(e){
        let objError = validate({...input, [e.target.name]: e.target.value})
        let formState;
        if (objError.name || objError.min_weight || objError.max_weight || objError.min_height || objError.max_height || objError.tempsId || objError.min_life || objError.max_life) {
            formState = "incompleted"
        }else{
            formState = "completed"
        }
        setError(objError)
        return formState
    }

    async function formSubmit(e){
        e.preventDefault();
        let formState = validateErrors(e);
        if(formState === "completed"){
            await postNewDog(input)
            await dispatch(getDbDogs())
            await dispatch(getAllDogs())
            setIncompleted(false)
            setInput({
                name: "",
                min_weight: "",
                max_weight: "",
                min_height: "",
                max_height: "",
                tempsId: [],
                min_life: "",
                max_life: "",
                image: "",
            })
            setPreview("")
        }else{
            setIncompleted(true)
            let objError = validate(input)
            setError(objError)
        } 
    }


    

    return(
        <div>
            <h3>Create Dog</h3>
            <form onSubmit={formSubmit}>
                <div>
                    <label htmlFor="name">Nombre:</label>
                    <input type="text" name="name" value={input.name}onChange={handleInput}/>
                    {error.name && <span>{error.name}</span> }
                </div>
                <div>
                    <h4>Peso:</h4>
                    <div>
                        <label htmlFor="min_weight">Minimo(Kg):</label>
                        <input type="number" name="min_weight" value={input.min_weight}onChange={handleInput}/>
                        {error.min_weight && <span>{error.min_weight}</span>}
                    </div>
                    <div>
                        <label htmlFor="max_weight">Maximo(Kg):</label>
                        <input type="number" name="max_weight" value={input.max_weight}onChange={handleInput}/>
                        {error.max_weight && <span>{error.max_weight}</span>}
                    </div>
                </div>
                <div>
                    <h4>Altura:</h4>
                    <div>
                        <label htmlFor="min_height">Minima(Cm):</label>
                        <input type="number" name="min_height" value={input.min_height}onChange={handleInput}/>
                        {error.min_height && <span>{error.min_height}</span>}
                    </div>
                    <div>
                        <label htmlFor="max_height">Maxima(Cm)</label>
                        <input type="number" name="max_height" value={input.max_height}onChange={handleInput}/>
                        {error.max_height && <span>{error.max_height}</span>}
                    </div>
                </div>
                <div>
                    <h4>Esperanza de Vida:</h4>
                    <div>
                        <label htmlFor="min_life">Minima:</label>
                        <input type="number" name="min_life" value={input.min_life}onChange={handleInput}/>
                        {error.min_life && <span>{error.min_life}</span>}
                    </div>
                    <div>
                        <label htmlFor="max_life">Maxima</label>
                        <input type="number" name="max_life" value={input.max_life}onChange={handleInput}/>
                        {error.max_life && <span>{error.max_life}</span>}
                    </div>
                </div>
                <div>
                    <div>
                        {preview && <img src={preview} />}
                    </div>
                    <input id="file" type="file" name="image" accept="image/*" onChange={handleInput}/>
                </div>
                <div>
                    <h4>Temperamentos:</h4>
                    <label htmlFor="tempsId"></label>
                    <select name="tempsId" value={input.tempsId.length > 0 ? input.tempsId[input.tempsId.length - 1] : ""} multiple={false} onChange={handleInput}>
                        <option value={""}>Select Tempers</option>
                        {temperaments.map(t => <option value={t.id}>{t.name}</option> )}
                    </select>
                    {available === true && <span>Ese temperamento ya se encuentra disponible</span>}
                </div>
                {error.tempsId && <span>{error.tempsId}</span>}
                <input type="submit" value={"Crear Perro"} />
                {incompleted === true && <span>Falta completar datos</span>}
                {incompleted === false && <span>Perro creado con exito</span>}
            </form>
        </div>
    )
}